.signUpContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-color: #1E40AF;
    padding: 20px;
    width: 95%;
    margin-top: 2%;
}

.signUpCard {
    background-color: white;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
}

.title {
    color: #1E40AF;
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2rem;
    font-weight: 600;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.formGroup {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.formGroup label {
    color: #4B5563;
    font-weight: 500;
}

.formGroup input {
    padding: 0.75rem;
    border: 2px solid #E5E7EB;
    border-radius: 6px;
    font-size: 1rem;
    transition: border-color 0.2s ease;
}

.formGroup input:focus {
    outline: none;
    border-color: #1E40AF;
}

.submitButton {
    background-color: #1E40AF;
    color: white;
    padding: 0.75rem;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s ease;
    margin-top: 1rem;
}

.submitButton:hover {
    background-color: #1C3879;
}

@media (max-width: 480px) {
    .signUpCard {
        padding: 1.5rem;
    }
    
    .title {
        font-size: 1.75rem;
    }
}

.formGroup textarea {
    padding: 0.75rem;
    border: 2px solid #E5E7EB;
    border-radius: 6px;
    font-size: 1rem;
    transition: border-color 0.2s ease;
    resize: vertical;
    min-height: 80px;
    font-family: inherit;
}

.formGroup textarea:focus {
    outline: none;
    border-color: #1E40AF;
}