.container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #1E40AF;
    padding: 20px;
}

.card {
    background: white;
    border-radius: 12px;
    padding: 2rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 90%;
    text-align: center;
}

.title {
    color: #EF4444;
    font-size: 2rem;
    margin-bottom: 1rem;
    font-weight: 600;
}

.message {
    color: #4B5563;
    font-size: 1rem;
    line-height: 1.5;
    margin-bottom: 2rem;
}

.retryButton {
    background-color: #10B981;
    color: white;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.retryButton:hover {
    background-color: #059669;
}

@media (max-width: 480px) {
    .card {
        padding: 1.5rem;
    }
    
    .title {
        font-size: 1.75rem;
    }
    
    .message {
        font-size: 0.875rem;
    }
    
    .retryButton {
        padding: 0.5rem 1.5rem;
    }
}
.loader {
    width: 48px;
    height: 48px;
    border: 5px solid #FFF;
    border-bottom-color: #1E40AF;
    border-radius: 50%;
    display: inline-block;
    margin: 1rem auto;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}