/* Modal styles */
.loginModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensure it's on top of everything */
  }
  
  .loginModalContent {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    width: 400px; /* Wider for better spacing */
    position: relative;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* More pronounced shadow */
  }
  
  .closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px; /* Larger close button */
    cursor: pointer;
    color: #777; /* Muted color */
    transition: color 0.3s ease;
  }
  
  .closeButton:hover {
    color: #333; /* Darken on hover */
  }
  
  .loginModalContent h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  .formGroup {
    margin-bottom: 20px; /* More spacing */
  }
  
  .formGroup label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    color: #555;
  }
  
  .formGroup input {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 6px; /* Slightly rounded corners */
    font-size: 16px;
    transition: border-color 0.3s ease;
  }
  
  .formGroup input:focus {
    outline: none;
    border-color: #3498db; /* Highlight on focus */
  }
  
  .loginModalContent button {
    background-color: #3498db;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
    transition: background-color 0.3s ease;
  }
  
  .loginModalContent button:hover {
    background-color: #2980b9;
  }
  
  /* Optional: Add some animation */
  .loginModalContent {
    animation: fadeIn 0.4s ease-in-out;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  

  /* footer */

  .footerContainer {
    margin-top: 20px;
    border-top: 1px solid rgba(255, 255, 255, 0.3);
    padding-top: 25px;
    position: sticky;
    bottom: 0;
    left: 0;
    width: 93%;
    /* font-size: 20%; */
    /* background-color: #3498db; A nice blue */
    color: white;
    background-color: rgb(247, 247, 247,0.5);
    padding: 10px 10px;
    border-radius: 0 0 20px 20px;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.2);
}

.footer {
  margin-right: 12px;
    display: flex;
    justify-content:space-evenly;
    align-items: center;
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.3);
    padding: 20px;
    border-radius: 12px;
    background: linear-gradient(135deg, rgba(30, 64, 175, 0.9), rgba(37, 99, 235, 0.8));
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(8px);
}

.footerItem {
  /* border:1px solid black; */
    position: relative;
    padding: 0 25px;
    font-size: 120%;
    font-weight: 500;
    /* margin:0; */
    cursor: pointer;
    transition: all 0.3s ease;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.footerItem:not(:last-child)::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    height: 25px;
    width: 2px;
    background: rgba(255, 255, 255, 0.3);
}

.footerItem:hover {
    color: #86d0b7;
    transform: translateY(-2px);
}

@media (max-width: 480px) {
    .footer {
        padding: 15px 10px;
    }
    
    .footerItem {
        padding: 0 15px;
        font-size: 1.25rem;
    }
}